import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import RecentWorks from "../screens/main/recent-works";
import MoveForward from "../components/common/move-forward";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const Portfolio = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Portfolio", link: null },
  ];
  return (
    <Layout>
      <BreadCrumb items={breadcrumbLinks} />
      <div className="mt-5">
        <RecentWorks />
        <MoveForward db={db} />
      </div>
    </Layout>
  );
};

export const Head = () => (
  <Seo title="Portfolio | justtrytech.com" headTitle="Portfolio Justtry"></Seo>
);

export default Portfolio;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
